import AclGuard from "components/AclGuard";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import RichTextEditor from "components/RichTextEditor/RichTextEditor";
import SaveComponent from "components/Save/SaveComponent";
import SkeletonCustom from "components/Skeleton/Skeleton";
import ToastCustom from "components/Toast";
import React, { forwardRef, useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import CamService from "services/transaction/CamService";
import SimpleReactValidator from "simple-react-validator";
import {
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
} from "utils/CommonUtil";
import { PAGE_MODE, STATIC_MASTER_TYPE_TOAST_TEXT, TOAST_STYLE_TYPE } from "utils/Constant";

export interface ICamRepaymentHistoryCommentPageObj {
  uid: string;
  editorText: string;
  errorMessage?: string;
}

export const initialRepaymentHistoryCommentPageObj: ICamRepaymentHistoryCommentPageObj = {
  uid: "",
  editorText: "",
  errorMessage: "",
};

interface IEditorRef {
  editor: {
    getContent: () => void;
    plugins: {
      wordcount: {
        body: {
          getCharacterCount: () => any;
        };
      };
    };
  };
}

interface IProps {
  isLoading: boolean;
  data: ICamRepaymentHistoryCommentPageObj;
  getDetailsCallback?: () => void;
}

const RepaymentHistoryComment: React.FC<IProps> = forwardRef((props, ref) => {
  const { data, isLoading, getDetailsCallback } = props;

  const params: { loanUid?: string; stageUid?: string } = useParams();
  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
  */
  const [pageObj, setPageObj] = useState<ICamRepaymentHistoryCommentPageObj>({
    ...initialRepaymentHistoryCommentPageObj,
  });
  const [pageMode, setPageMode] = useState(PAGE_MODE.VIEW);
  const [toastMessage, setToastMessage] = useState("");
  const [triggerToast, setTriggerToast] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);

  // ref
  const toastMessageRef = useRef(null);
  const editorRef = useRef<IEditorRef>(null);

  // --------------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false);

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  useEffect(() => {
    // if (data.loanUid) {
    setPageObj((prevObj) => ({
      ...prevObj,
      ...data,
    }));
    // }
  }, [data]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }, [toastMessage, triggerToast]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */
  const handleEditorValidation = (e: any) => {
    if (validateEditor()) {
      if (pageObj?.errorMessage) {
        setPageObj((prevPageObj) => ({ ...prevPageObj, errorMessage: "" }));
      }
    }
  };

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  const validateForm = () => {
    let isValid = false;
    isValid = simpleValidator?.current?.allValid() ?? false;
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  const validateEditor = () => {
    console.log("editor ", editorRef);

    const wordcount = editorRef?.current?.editor?.plugins?.wordcount?.body.getCharacterCount();

    if (wordcount < 1) {
      setPageObj((prevState) => {
        return {
          ...prevState,
          errorMessage: "Mandatory",
        };
      });
      return false;
    }

    return true;
  };

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  const onSave = async () => {
    if (validateEditor()) {
      const payload = enrichForSave();
      try {
        await CamService.updateRepaymentHistoryComment(payload);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
        setTriggerToast(!triggerToast);
        setPageMode(PAGE_MODE.VIEW);
        getDetailsCallback?.();
      } catch (error) {
        setErrorMessage(parseStandardAPIErrorMessage(error));
      }
    }
  };

  const onEdit = () => setPageMode(PAGE_MODE.EDIT);

  const onCancel = () => {
    setPageMode(PAGE_MODE.VIEW);
    resetValidator();
    // if  form has existing saved  data then call api else clear all
    getDetailsCallback?.();
  };

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  const enrichForSave = () => {
    return {
      loan: {
        uid: params?.loanUid,
      },
      stage: {
        uid: params?.stageUid,
      },
      remarksOnRepaymentHistory: editorRef.current?.editor?.getContent(),
    };
  };

  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  /*
    -------------------------------------------------------
      Sectional Renders
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
    Main Renders
    -------------------------------------------------------
  */

  return (
    <div>
      <SaveComponent
        title={"label.cam.commentsOnRepaymentHistory"}
        pageMode={pageMode}
        disabled={isLoading}
        onSave={onSave}
        onCancel={onCancel}
        onEdit={onEdit}
      >
        {isLoading ? (
          <SkeletonCustom height="19.375rem" width="100%" borderRadius="8px" animation="wave" />
        ) : (
          <AclGuard override={{ read: pageMode === PAGE_MODE.VIEW }}>
            <div className="grid grid-cols-4 gap-3 ">
              <div className="col-span-4">
                <RichTextEditor
                  ref={editorRef}
                  validation={{ errorMessage: pageObj.errorMessage }}
                  initialValue={pageObj.editorText}
                  onChange={handleEditorValidation}
                  height={"400x"}
                  disabled={pageMode === PAGE_MODE.VIEW}
                />
              </div>
            </div>
          </AclGuard>
        )}
      </SaveComponent>

      <ToastCustom
        position={"bottom-right"}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={TOAST_STYLE_TYPE.PRIMARY}
      />

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </div>
  );
});

export default RepaymentHistoryComment;
