import AclGuard from "components/AclGuard";
import { EditableComponentRef } from "components/EditableTableV2/EditableTableV2";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import InputBox from "components/InputBox";
import SaveComponent from "components/Save/SaveComponent";
import SimpleDropDown from "components/SimpleDropDown/SimpleDropDown";
import SkeletonCustom from "components/Skeleton/Skeleton";
import ToastCustom from "components/Toast";
import { numberFormatter } from "locale";
import React, { forwardRef, useEffect, useRef, useState } from "react";
import LegalService from "services/transaction/DDE/LegalService";
import SimpleReactValidator from "simple-react-validator";
import {
  currencyFormatToNumber,
  currencyFormatToNumberIncludeNull,
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
} from "utils/CommonUtil";
import {
  INPUT_STYLE_TYPE,
  PAGE_MODE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
  YES_NO,
  YES_NO_OPTIONS,
} from "utils/Constant";
import { getNumber, getString, showCurrency } from "../../Helper";
import { ICamLoanDetailsDropdownList } from "../../PageLoanDetails/PageLoanDetails";
import { useParams } from "react-router-dom";
import CamService from "services/transaction/CamService";

export interface ICamFinalLoanDetailsPageObj {
  recommendedLoanAmount: number | string;
  insurancePremium: number | string;
  totalLoanAmount: number | string;
  roi: number | string;
  tenure: string;
  interestType: string;
  emi: string | number;
  totalDeduction: number | string;
  netDisbursal: string | number;
  transactionType: string;
  balanceTransfer: string;
  employeeLoan: string;
  employeeId: string;
  employeeName: string;
  isCalculating?: boolean;
  // properties: ICamSinglePropertyValuationData[];
  // totalValuation: number | "";
}
interface IProps {
  isLoading: boolean;
  dropDownOptions: ICamLoanDetailsDropdownList;
  data: ICamFinalLoanDetailsPageObj;
  getDetailsCallback?: () => void;
}

export const initialCamFinalLoanDetailsData: ICamFinalLoanDetailsPageObj = {
  recommendedLoanAmount: "",
  insurancePremium: "",
  totalLoanAmount: "",
  roi: 0,
  tenure: "",
  interestType: "",
  emi: "",
  totalDeduction: "",
  netDisbursal: "",
  transactionType: "",
  balanceTransfer: "",
  employeeLoan: "",
  employeeId: "",
  employeeName: "",
  isCalculating: false,
  // totalValuation: "",
};

const transactionTypeBalanceTransfer = "TRANSACTION_TYPE_BALANCE_TRANSFER";

const FinalLoanDetails: React.FC<IProps> = forwardRef((props, ref) => {
  const { data, dropDownOptions, isLoading, getDetailsCallback } = props;
  const params: { loanUid?: string; stageUid?: string } = useParams();
  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
    */
  const [pageObj, setPageObj] = useState<ICamFinalLoanDetailsPageObj>({
    ...initialCamFinalLoanDetailsData,
  });
  const [timeOutLocal, setTimeOutLocal] = useState<any>(undefined);
  const [pageMode, setPageMode] = useState(PAGE_MODE.VIEW);
  const [toastMessage, setToastMessage] = useState("");
  const [triggerToast, setTriggerToast] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  // ref
  const toastMessageRef = useRef(null);
  const tableRef = React.useRef<EditableComponentRef>(null);

  // --------------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false);

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  useEffect(() => {
    setPageObj((prevObj) => ({
      ...prevObj,
      ...data,
    }));
    // when add the properties  add the  validations for the properties
    // resetAllSectionValidations(data?.properties?.length);
  }, [data]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }, [toastMessage, triggerToast]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);
  // pageObj

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  const calculate = async (data: ICamFinalLoanDetailsPageObj) => {
    setPageObj((prevObj) => ({
      ...prevObj,
      isCalculating: true,
    }));
    try {
      const payload = enrichForCalculation(data);
      const res = await CamService.engine(payload);
      // data

      setPageObj((prevObj) => ({
        ...prevObj,
        isCalculating: false,
        totalLoanAmount: showCurrency(res?.data?.totalLoanAmount),
        emi: showCurrency(res?.data?.emi),
        totalDeduction: showCurrency(res?.data?.totalDeductions),
        netDisbursal: showCurrency(res?.data?.netDisbursal),
      }));
      // tableRef.current?.reportDataChange(updatedRowDataObj);
    } catch (err) {
      console.log(err);
      setPageObj((prevObj) => ({
        ...prevObj,
        isCalculating: false,
      }));
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */

  const handleChange = (
    e: any,
    options?: { currency?: true; field?: keyof ICamFinalLoanDetailsPageObj }
  ) => {
    setPageObj((prevObj) => {
      let updatedPageObj = { ...prevObj };
      if (options?.currency) {
        // const unformattedValue = currencyFormatToNumber(e.target.value);
        updatedPageObj = {
          ...updatedPageObj,
          [e.target.name]: showCurrency(e.target.value),
        };
      } else {
        updatedPageObj = { ...updatedPageObj, [e.target.name]: e.target.value };
      }

      if (options?.field === "employeeLoan") {
        if (e?.target?.value === YES_NO.NO) {
          updatedPageObj = {
            ...updatedPageObj,
            employeeId: "",
            employeeName: "",
          };
        }
      }
      if (
        e.target.name === "transactionType" &&
        e.target.value !== "TRANSACTION_TYPE_BALANCE_TRANSFER"
      ) {
        updatedPageObj = { ...updatedPageObj, balanceTransfer: "0" };
      }

      if (
        options?.field === "recommendedLoanAmount" ||
        options?.field === "roi" ||
        options?.field === "tenure"
      ) {
        debounce(() => calculate(updatedPageObj), 1200);
      }
      return updatedPageObj;
    });
  };

  // const handleRecommendedLoanAmountChange = (e: any) => {
  //   setPageObj((prevObj) => {
  //     let updatedPageObj = { ...prevObj };
  //     const unformattedValue = currencyFormatToNumber(e.target.value);
  //     updatedPageObj = {
  //       ...updatedPageObj,
  //       recommendedLoanAmount: numberFormatter(unformattedValue, "en-IN", 0),
  //     };
  //     debounce(() => calculate(updatedPageObj), 1200);
  //     return updatedPageObj;
  //   });
  // };

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;
    isValid = simpleValidator?.current?.allValid() ?? false;

    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };
  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  const onSave = async () => {
    if (!validateForm()) {
      return;
    }
    const payload = enrichForSave();
    try {
      await CamService.updateFinalLoanDetails(payload);
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
      setTriggerToast(!triggerToast);
      setPageMode(PAGE_MODE.VIEW);
      getDetailsCallback?.();
    } catch (error) {
      setErrorMessage(parseStandardAPIErrorMessage(error));
    }
  };

  const onEdit = () => setPageMode(PAGE_MODE.EDIT);

  const onCancel = () => {
    setPageMode(PAGE_MODE.VIEW);
    resetValidator();
    // if  form has existing saved  data then call api else clear all
    getDetailsCallback?.();
  };

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  const enrichForCalculation = (data: ICamFinalLoanDetailsPageObj) => {
    const enrichObj = {
      loanUid: params?.loanUid,
      stageUid: params?.stageUid,
      recommendedLoanAmount: getNumber(data?.recommendedLoanAmount),
      roi: getNumber(data?.roi),
      tenure: getNumber(data?.tenure),
    };
    return enrichObj;
  };

  const debounce = (func: any, delay: any) => {
    // for amount incl gst calc
    clearTimeout(timeOutLocal);
    setTimeOutLocal(setTimeout(func, delay));
  };

  const enrichForSave = () => {
    const enrichObj = {
      loan: {
        uid: params?.loanUid,
      },
      stage: {
        uid: params?.stageUid,
      },
      recommendedLoanAmount: getNumber(pageObj?.recommendedLoanAmount),
      insurancePremium: getNumber(pageObj?.insurancePremium),
      totalLoanAmount: getNumber(pageObj?.totalLoanAmount),
      roi: getNumber(pageObj?.roi),
      tenure: pageObj?.tenure,
      smInterestType: pageObj?.interestType,
      emi: getNumber(pageObj?.emi),
      totalDeductions: getNumber(pageObj?.totalDeduction),
      netDisbursal: getNumber(pageObj?.netDisbursal),
      smTransactionType: pageObj?.transactionType,
      balanceTransferInMonths: pageObj?.balanceTransfer,
      smEmployeeLoan: pageObj?.employeeLoan,
      employeeId: pageObj?.employeeId,
      employeeName: pageObj?.employeeName,
    };
    return enrichObj;
  };

  const getValidationForEmployeeID = () => {
    if (pageObj?.employeeLoan === YES_NO.YES) {
      // return "mandatory|onlyAlphaNumUnderscoreUpperScore"
      return "mandatory";
    }
    return "optional";
  };

  const getValidationForEmployeeName = () => {
    if (pageObj?.employeeLoan === YES_NO.YES) {
      return "mandatory";
    }
    return "optional";
  };

  const getValidationForBalanceTransfer = () => {
    if (pageObj?.transactionType === transactionTypeBalanceTransfer) {
      return "mandatory";
    }
    return "optional";
  };

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */
  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */
  const {
    recommendedLoanAmount,
    insurancePremium,
    totalLoanAmount,
    roi,
    tenure,
    interestType,
    emi,
    totalDeduction,
    netDisbursal,
    transactionType,
    balanceTransfer,
    employeeLoan,
    employeeId,
    employeeName,
  } = pageObj;
  console.log("pageObj====", pageObj);

  return (
    <>
      <SaveComponent
        title="label.summary.eligibility.finalLoanDetails"
        pageMode={pageMode}
        disabled={pageObj?.isCalculating}
        onSave={onSave}
        onCancel={onCancel}
        onEdit={onEdit}
      >
        {isLoading ? (
          <SkeletonCustom height="19.375rem" width="100%" borderRadius="8px" animation="wave" />
        ) : (
          <AclGuard override={{ read: pageMode === PAGE_MODE.VIEW }}>
            <div className=" grid grid-cols-4 gap-3  ">
              <div className="">
                <InputBox
                  label="label.cam.recommendedLoanAmt*"
                  name="recommendedLoanAmount"
                  variant={INPUT_STYLE_TYPE.SECONDARY}
                  value={recommendedLoanAmount ?? ""}
                  onChange={(e) =>
                    handleChange(e, { currency: true, field: "recommendedLoanAmount" })
                  }
                  className="text-right"
                  validation={simpleValidator?.current?.message(
                    `recommendedLoanAmount`,
                    currencyFormatToNumberIncludeNull(recommendedLoanAmount),
                    "mandatory|checkNumber|onlyPositive"
                  )}
                  onBlur={validate}
                />
              </div>
              <div>
                <InputBox
                  label="label.cam.insurancePremium*"
                  name="insurancePremium"
                  className="text-right"
                  variant={INPUT_STYLE_TYPE.SECONDARY}
                  value={insurancePremium ?? ""}
                  // onChange={handleChange}
                  disabled
                />
              </div>
              <div>
                <InputBox
                  label="label.cam.totalLoanAmount*"
                  name="totalLoanAmount"
                  className="text-right"
                  variant={INPUT_STYLE_TYPE.SECONDARY}
                  value={totalLoanAmount ?? ""}
                  disabled
                />
              </div>
              <div>
                <InputBox
                  label="label.cam.ROI*"
                  name="roi"
                  variant={INPUT_STYLE_TYPE.SECONDARY}
                  value={roi ?? ""}
                  className="text-right"
                  onChange={(e) => handleChange(e, { currency: true, field: "roi" })}
                  validation={simpleValidator?.current?.message(
                    `roi`,
                    currencyFormatToNumberIncludeNull(roi),
                    "mandatory|checkNumber|onlyPositive"
                  )}
                  onBlur={validate}
                />
              </div>
              <div>
                <InputBox
                  label="label.cam.tenure(Months)*"
                  name="tenure"
                  className="text-right"
                  variant={INPUT_STYLE_TYPE.SECONDARY}
                  value={tenure ?? ""}
                  onChange={(e) => handleChange(e, { field: "tenure" })}
                  validation={simpleValidator?.current?.message(`tenure`, tenure, "mandatory")}
                  onBlur={validate}
                />
              </div>
              <div>
                <SimpleDropDown
                  label="label.cam.interestType*"
                  placeholder="placeholder.cam.interestType"
                  name="interestType"
                  value={interestType}
                  onChange={handleChange}
                  validation={simpleValidator?.current?.message(
                    `interestType`,
                    interestType,
                    "mandatory"
                  )}
                  onBlur={validate}
                  options={dropDownOptions.interestType}
                  optionLabel="name"
                  optionValue="code"
                  // disabled={productIncomeScheme === ""}
                />
              </div>
              <div>
                <InputBox
                  label="label.cam.emi*"
                  name="emi"
                  className="text-right"
                  variant={INPUT_STYLE_TYPE.SECONDARY}
                  value={emi ?? ""}
                  disabled
                />
              </div>
              <div>
                <InputBox
                  label="label.cam.TotalDeductions*"
                  name="totalDeduction"
                  className="text-right"
                  variant={INPUT_STYLE_TYPE.SECONDARY}
                  value={totalDeduction ?? ""}
                  onChange={(e) => handleChange(e, { currency: true })}
                  disabled
                />
              </div>
              <div>
                <InputBox
                  label="label.cam.netDisbursal*"
                  name="netDisbursal"
                  className="text-right"
                  variant={INPUT_STYLE_TYPE.SECONDARY}
                  value={netDisbursal ?? ""}
                  disabled
                />
              </div>
              <div>
                <SimpleDropDown
                  label="label.cam.transactionType*"
                  placeholder="placeholder.cam.transactionType"
                  name="transactionType"
                  value={transactionType}
                  onChange={(e: any) => handleChange(e, { field: "transactionType" })}
                  validation={simpleValidator?.current?.message(
                    `transactionType`,
                    transactionType,
                    "mandatory"
                  )}
                  onBlur={validate}
                  options={dropDownOptions.transactionType}
                  optionLabel="name"
                  optionValue="code"
                  // disabled={productIncomeScheme === ""}
                />
              </div>
              <div>
                <InputBox
                  label="label.cam.balanceTransfer(Months)*"
                  name="balanceTransfer"
                  className="text-right"
                  variant={INPUT_STYLE_TYPE.SECONDARY}
                  value={balanceTransfer ?? ""}
                  onChange={handleChange}
                  validation={simpleValidator?.current?.message(
                    `balanceTransfer`,
                    balanceTransfer,
                    getValidationForBalanceTransfer()
                  )}
                  onBlur={validate}
                  disabled={pageObj?.transactionType !== transactionTypeBalanceTransfer}
                />
              </div>
              <div>
                <SimpleDropDown
                  label="label.cam.employeeLoan*"
                  placeholder="placeholder.cam.employeeLoan"
                  name="employeeLoan"
                  value={employeeLoan}
                  onChange={(e) => handleChange(e, { field: "employeeLoan" })}
                  validation={simpleValidator?.current?.message(
                    `employeeLoan`,
                    employeeLoan,
                    "mandatory"
                  )}
                  onBlur={validate}
                  options={YES_NO_OPTIONS}
                  optionLabel="label"
                  optionValue="value"
                />
              </div>
              <div>
                <InputBox
                  label={employeeLoan === "YES" ? "label.cam.employeeID*" : "label.cam.employeeID"}
                  name="employeeId"
                  variant={INPUT_STYLE_TYPE.SECONDARY}
                  value={employeeId ?? ""}
                  onChange={handleChange}
                  validation={simpleValidator?.current?.message(
                    `employeeId`,
                    employeeId,
                    getValidationForEmployeeID()
                  )}
                  onBlur={validate}
                  disabled={pageObj?.employeeLoan === YES_NO.NO}
                />
              </div>
              <div>
                <InputBox
                  label={
                    employeeLoan === "YES" ? "label.cam.employeeName*" : "label.cam.employeeName"
                  }
                  name="employeeName"
                  variant={INPUT_STYLE_TYPE.SECONDARY}
                  value={employeeName ?? ""}
                  onChange={handleChange}
                  validation={simpleValidator?.current?.message(
                    `employeeName`,
                    employeeName,
                    getValidationForEmployeeName()
                  )}
                  onBlur={validate}
                  disabled={pageObj?.employeeLoan === YES_NO.NO}
                />
              </div>
            </div>
          </AclGuard>
        )}
      </SaveComponent>

      <ToastCustom
        position={"bottom-right"}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={TOAST_STYLE_TYPE.PRIMARY}
      />

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </>
  );
});

export default FinalLoanDetails;
