import AclGuard from "components/AclGuard";
import ButtonBox from "components/ButtonBox/ButtonBox";
import EditableTableV2, {
  Column,
  EditableComponentRef,
} from "components/EditableTableV2/EditableTableV2";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import IconButton, { BUTTON_TYPES } from "components/IconButton/IconButton";
import InputAddon from "components/InputAddon/InputAddon";
import InputBox from "components/InputBox";
import SaveComponent from "components/Save/SaveComponent";
import SimpleDropDown from "components/SimpleDropDown/SimpleDropDown";
import SkeletonCustom from "components/Skeleton/Skeleton";
import Text from "components/Text/Text";
import TextButton from "components/TextButton/TextButton";
import ToastCustom from "components/Toast";
import i18n from "i18n";
import { numberFormatter } from "locale";
import React, { forwardRef, useEffect, useImperativeHandle, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import CamService from "services/transaction/CamService";
import SimpleReactValidator from "simple-react-validator";
import {
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
} from "utils/CommonUtil";
import {
  BUTTON_TYPE,
  CAM_CHARGE_CONFIGURATION_CODE,
  CAM_CHARGE_TYPE,
  CAM_FEE_STATUS,
  INPUT_STYLE_TYPE,
  PAGE_MODE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
} from "utils/Constant";
import { getNumber, showCurrency, updateCamFeeDetailsRowDataObj } from "../../Helper";
import { ICamLoanDetailsDropdownList } from "../../PageLoanDetails/PageLoanDetails";
import WarningDialog from "components/WarningDialog/WarningDialog";

export type TCamFeeStatus = "APPLICABLE";
export type TCamChargeTypeCode = "CHARGE_TYPE_SYSTEM_DEFINED" | "CHARGE_TYPE_USER_DEFINED";
export type TCamConfigurationTypeCode = "CONFIGURATION_FIXED" | "CONFIGURATION_VARIABLE";
export interface ICamProductIncomeSchemeDropdownOption {
  uid?: string;
  name?: string;
  product?: {
    uid?: string;
    name?: string;
    productType?: {
      uid?: string;
      name?: string;
    };
  };
  incomeSchemeName?: {
    uid?: string;
    name?: string;
  };
  bank?: {
    uid?: string;
    name?: string;
  };
  lendingType?: {
    uid?: string;
    name?: string;
  };
  details?: {
    maxFoir?: number | string;
  }[];
}

export interface ICamChargeNameDropdownOption {
  uid?: string; // fee configuration Uid
  // name?:string;
  fee?: {
    name?: string; // display name
    chargeType?: {
      name?: string;
      code?: TCamChargeTypeCode;
    };
    configurationType?: {
      name?: string;
      code?: TCamConfigurationTypeCode;
    };
  };
  isWaiver: boolean;
  isDefault: boolean;
  amount: string | number;
  amountInPercent: string;
}
export interface ICamSchemeDetailsPageObj {
  tableSearch?: string;
  productIncomeScheme: ICamProductIncomeSchemeDropdownOption;
  loadingForUpdatedProductIncomeScheme?: boolean;
  chargeNameOptions?: ICamChargeNameDropdownOption[];
  row?: {
    isCalculating: boolean;
    uid: string;
  };
  isChangeProductIncomeScheme?: boolean;
  newProductIncomeScheme?: ICamProductIncomeSchemeDropdownOption;
  // delete
}

export interface ICamSchemeDetailsRef {
  refreshFeeTable: () => void;
}

interface IProps {
  isLoading: boolean;
  dropDownOptions: ICamLoanDetailsDropdownList;
  data: ICamSchemeDetailsPageObj;
  ref: any;
  getDetailsCallback?: (options?: { isProductIncomeSchemeUpdated?: boolean }) => void;
}

export interface ICamFeeDetailsRowData {
  uid: string;
  chargeTypeCode: TCamChargeTypeCode | "";
  amount: string | number;
  amountIncludingGst: string | number; // not present in the rowData sometimes
  feeConfiguration: {
    uid?: string; // update this uid on charge name change for payload
    fee?: {
      name?: string; // update this uid on charge name change for UI
    };
    configurationType?: {
      name?: string; // update this uid on charge name change for UI
      code?: TCamConfigurationTypeCode | ""; // update this field on charge name change for Payload
    };
    isWaiver?: boolean;
    isDefault?: boolean;
  };
  status: TCamFeeStatus;
  isActive: boolean;
}
const initialProductIncomeScheme = {
  uid: "",
  name: "",
  product: {
    uid: "",
    name: "",
  },
  bank: {
    uid: "",
    name: "",
  },
  incomeSchemeName: {
    uid: "",
    name: "",
  },
  lendingType: {
    uid: "",
    name: "",
  },
};

export const initialCamSchemeDetailsData: ICamSchemeDetailsPageObj = {
  tableSearch: "",
  productIncomeScheme: {
    ...initialProductIncomeScheme,
  },
  loadingForUpdatedProductIncomeScheme: false,
  chargeNameOptions: [],
  row: {
    isCalculating: false,
    uid: "",
  },
  isChangeProductIncomeScheme: false,
  newProductIncomeScheme: { ...initialProductIncomeScheme },
  // totalValuation: "",
};

const initialTableRowData: ICamFeeDetailsRowData = {
  uid: "",
  chargeTypeCode: "",
  amount: "",
  amountIncludingGst: "", // not present in the rowData
  feeConfiguration: {
    uid: "",
    fee: {
      name: "",
    },
    configurationType: {
      name: "",
      code: "",
    },
    isDefault: false,
    isWaiver: false,
  },
  status: CAM_FEE_STATUS.APPLICABLE,
  isActive: true,
};

const SchemeDetails: React.FC<IProps> = forwardRef((props, ref) => {
  const { data, dropDownOptions, isLoading, getDetailsCallback } = props;
  const params: { loanUid?: string; stageUid?: string } = useParams();
  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
    */
  const [pageObj, setPageObj] = useState<ICamSchemeDetailsPageObj>({
    ...initialCamSchemeDetailsData,
  });
  // const [pageObj, setPageObj] = useState({});
  const [pageMode, setPageMode] = useState(PAGE_MODE.VIEW);
  const [timeOutLocal, setTimeOutLocal] = useState<any>(undefined);
  const [toastMessage, setToastMessage] = useState("");
  const [triggerToast, setTriggerToast] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  // ref
  const toastMessageRef = useRef(null);
  const tableRef = React.useRef<EditableComponentRef>(null);

  // --------------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false);

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  useImperativeHandle(ref, () => ({
    refreshFeeTable: refreshFeeTable,
  }));

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  // useEffect(() => {
  //   setPageObj((prevPageObj) => ({
  //     ...prevPageObj,
  //     tableSearch: `&loanUid=${params?.loanUid}&reEvalDefaultFee=false`,
  //   }));
  // }, []);

  useEffect(() => {
    setPageObj((prevObj) => ({
      ...prevObj,
      productIncomeScheme: data?.productIncomeScheme,
      tableSearch: `&loanUid=${params?.loanUid}&reEvalDefaultFee=false&start=0&limit=10000`,
    }));
  }, [data]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }, [toastMessage, triggerToast]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  useEffect(() => {
    if (
      pageObj?.productIncomeScheme?.lendingType?.uid &&
      pageObj?.productIncomeScheme?.bank?.uid && //&&
      dropDownOptions?.productIncomeScheme &&
      dropDownOptions?.productIncomeScheme?.length > 0
    ) {
      const selectedProductIncomeScheme: ICamProductIncomeSchemeDropdownOption | undefined =
        dropDownOptions?.productIncomeScheme?.find(
          (record) => record.uid === pageObj?.productIncomeScheme?.uid
        );
      getChargeNameDropdownOptions(selectedProductIncomeScheme?.product?.productType?.uid ?? "");
    }
  }, [pageObj?.productIncomeScheme?.uid, dropDownOptions.productIncomeScheme]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  // implementation before old api
  // const getChargeNameDropdownOptions = async (productTypeUid:string) => {
  //   try {
  //     const res = await CamService.getChargeNameOptions(
  //       `?bank.uid=${pageObj?.productIncomeScheme?.bank?.uid}&lendingType.uid=${pageObj?.productIncomeScheme?.lendingType?.uid}&fee.productType.uid=${productTypeUid}&isDefault=false&fee.isActive=true&isActive=true`
  //     );

  //     setPageObj((prevObj) => {
  //       const updatedPageObj = { ...prevObj };
  //       updatedPageObj.chargeNameOptions = res?.data?.results;
  //       return updatedPageObj;
  //     });
  //   } catch (err) {
  //     console.log(err);
  //     setErrorMessage(parseStandardAPIErrorMessage(err));
  //   }
  // };

  const getChargeNameDropdownOptions = async (productTypeUid: string) => {
    try {
      const res = await CamService.getChargeNameOptions(
        `?bankUid=${pageObj?.productIncomeScheme?.bank?.uid}&lendingTypeUid=${pageObj?.productIncomeScheme?.lendingType?.uid}&productTypeUid=${productTypeUid}`
      );

      setPageObj((prevObj) => {
        const updatedPageObj = { ...prevObj };
        updatedPageObj.chargeNameOptions = res?.data;
        return updatedPageObj;
      });
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const getAmountIncludingGst = async (rowData: ICamFeeDetailsRowData) => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      row: {
        ...prevPageObj.row,
        isCalculating: true,
        uid: rowData?.uid,
      },
    }));
    try {
      const payload = enrichForCalculation(rowData);
      const res = await CamService.engine(payload);
      // loanFeeAmountIncludingGst

      setPageObj((prevObj) => ({
        ...prevObj,
        row: {
          ...prevObj.row,
          isCalculating: false,
          uid: "",
        },
      }));
      const allRecords = tableRef?.current?.getCurrentRecords();
      const editedRecord = allRecords?.find((record) => record.uid === rowData?.uid);
      const updatedRowDataObj: ICamFeeDetailsRowData = {
        ...editedRecord,
        amountIncludingGst: showCurrency(res?.data?.loanFeeAmountIncludingGst),
      };
      tableRef.current?.reportDataChange(updatedRowDataObj);
    } catch (err) {
      console.log(err);
      setPageObj((prevObj) => ({
        ...prevObj,
        row: {
          ...prevObj.row,
          isCalculating: false,
          uid: "",
        },
      }));
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */

  const handleChange = (e: any) => {
    setPageObj((prevObj) => ({
      ...prevObj,
      [e.target.name]: e.target.value,
    }));
  };

  const handleProductSchemeChange = (e: any) => {
    const selectedProductIncomeScheme = dropDownOptions?.productIncomeScheme?.find(
      (record) => record.uid === e.target.value
    );
    //data?.productIncomeScheme?.uid;
    setPageObj((prevObj) => {
      let updatePageObj = { ...prevObj };
      let updateKey: "newProductIncomeScheme" | "productIncomeScheme" = "productIncomeScheme";
      const updatesProductIncomeScheme: ICamProductIncomeSchemeDropdownOption = {
        ...prevObj.productIncomeScheme,
        name: selectedProductIncomeScheme?.name ?? "",
        uid: selectedProductIncomeScheme?.uid ?? "",
        product: {
          ...prevObj.productIncomeScheme.product,
          name: selectedProductIncomeScheme?.product?.name ?? "",
          uid: selectedProductIncomeScheme?.product?.uid ?? "",
        },
        incomeSchemeName: {
          ...prevObj.productIncomeScheme.incomeSchemeName,
          name: selectedProductIncomeScheme?.incomeSchemeName?.name ?? "",
          uid: selectedProductIncomeScheme?.incomeSchemeName?.uid ?? "",
        },
        bank: {
          ...prevObj.productIncomeScheme.bank,
          name: selectedProductIncomeScheme?.bank?.name ?? "",
          uid: selectedProductIncomeScheme?.bank?.uid ?? "",
        },
        lendingType: {
          ...(prevObj.productIncomeScheme.lendingType ?? ""),
          name: selectedProductIncomeScheme?.lendingType?.name ?? "",
          uid: selectedProductIncomeScheme?.lendingType?.uid ?? "",
        },
      };
      if (data?.productIncomeScheme?.uid) {
        updateKey = "newProductIncomeScheme";
        updatePageObj.isChangeProductIncomeScheme = true;
      }
      updatePageObj = {
        ...updatePageObj,
        [`${updateKey}`]: { ...updatesProductIncomeScheme },
      };

      return updatePageObj;
    });
  };

  const handleTableChargeNameChange = (
    e: any,
    options: {
      rowData: ICamFeeDetailsRowData;
    }
  ) => {
    let rowDataObj: ICamFeeDetailsRowData = { ...options.rowData };

    const selectedChargeName = pageObj?.chargeNameOptions?.find(
      (record) => record.uid === e.target.value
    );
    rowDataObj = {
      ...rowDataObj,
      feeConfiguration: {
        ...rowDataObj.feeConfiguration,
        uid: selectedChargeName?.uid ?? "",
        configurationType: {
          ...rowDataObj.feeConfiguration.configurationType,
          name: selectedChargeName?.fee?.configurationType?.name ?? "",
          code: selectedChargeName?.fee?.configurationType?.code ?? "",
        },
        isDefault: selectedChargeName?.isDefault,
        isWaiver: selectedChargeName?.isWaiver,
        fee: {
          ...rowDataObj.feeConfiguration.fee,
          name: selectedChargeName?.fee?.name,
        },
      },
      amount:
        selectedChargeName?.fee?.configurationType?.code ===
        CAM_CHARGE_CONFIGURATION_CODE.CONFIGURATION_FIXED
          ? selectedChargeName?.amount
          : selectedChargeName?.amountInPercent ?? "",
      chargeTypeCode: selectedChargeName?.fee?.chargeType?.code ?? "",
    };
    debounce(() => getAmountIncludingGst(rowDataObj), 1200);
    tableRef.current?.reportDataChange(rowDataObj);
  };

  const handleTableAmountChange = (
    e: any,
    options: {
      rowData: ICamFeeDetailsRowData;
    }
  ) => {
    let rowDataObj: ICamFeeDetailsRowData = { ...options.rowData };
    let updatedAmount;
    if (
      options?.rowData?.feeConfiguration?.configurationType?.code ===
      CAM_CHARGE_CONFIGURATION_CODE.CONFIGURATION_FIXED
    ) {
      updatedAmount = showCurrency(e.target.value);
    } else {
      updatedAmount = e.target.value;
    }
    // const unformattedValue = currencyFormatToNumber(e.target.value);
    rowDataObj = {
      ...rowDataObj,
      amount: updatedAmount,
    };
    tableRef.current?.reportDataChange(rowDataObj);
    debounce(() => getAmountIncludingGst(rowDataObj), 1200);
  };

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;
    isValid = simpleValidator?.current?.allValid() ?? false;
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  const onSave = async () => {
    if (!validateForm()) {
      return;
    }
    const payload = enrichForSave();
    try {
      await CamService.updateSchemeDetails(payload);
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
      setTriggerToast(!triggerToast);
      setPageMode(PAGE_MODE.VIEW);
      getDetailsCallback?.();
      tableRef.current?.clearAll();
      tableRef.current?.search();
    } catch (error) {
      setErrorMessage(parseStandardAPIErrorMessage(error));
    }
  };

  const onEdit = () => setPageMode(PAGE_MODE.EDIT);

  const onCancel = () => {
    setPageMode(PAGE_MODE.VIEW);
    resetValidator();
    getDetailsCallback?.();
    tableRef.current?.clearAll();
    tableRef.current?.search();
  };

  const onAddDocument = () => {
    tableRef.current?.addRow({ ...initialTableRowData });
  };

  const onPrePopulateData = async () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      tableSearch: `&loanUid=${params?.loanUid}&reEvalDefaultFee=true&bankUid=${pageObj?.productIncomeScheme?.bank?.uid}&lendingTypeUid=${pageObj?.productIncomeScheme?.lendingType?.uid}&start=0&limit=10000`,
    }));
  };

  const onDeleteRow = (rowData: ICamFeeDetailsRowData) => {
    tableRef.current?.deleteRow(rowData);
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
  };

  const onTableUpdateCallback = () => {
    resetValidator();
    if (pageObj?.tableSearch?.includes(`reEvalDefaultFee=true`)) {
      const allTableRows = tableRef?.current?.getCurrentRecords();
      allTableRows?.forEach((record) => {
        if (!record?.uid) {
          getAmountIncludingGst(record);
        }
      });
    }
  };

  const onHideWarningPopup = () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      isChangeProductIncomeScheme: false,
      newProductIncomeScheme: { ...initialProductIncomeScheme },
    }));
  };

  const onConfirmDeleteTab = async () => {
    console.log("test");
    // const payload = enrichForSave();
    const newProductIncomeSchemeUid = pageObj?.newProductIncomeScheme?.uid;
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      isChangeProductIncomeScheme: false,
      productIncomeScheme: { ...prevPageObj.newProductIncomeScheme },
      newProductIncomeScheme: { ...initialProductIncomeScheme },
      loadingForUpdatedProductIncomeScheme: true,
    }));
    try {
      // await CamService.deactivateSelectedFeesForProductIncomeScheme(params?.loanUid ?? "");
      const payload = enrichForSaveOnProductIncomeSchemeUpdate(newProductIncomeSchemeUid);
      await CamService.updateSchemeDetails(payload);
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        loadingForUpdatedProductIncomeScheme: false,
      }));
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
      setTriggerToast(!triggerToast);
      getDetailsCallback?.({ isProductIncomeSchemeUpdated: true });
      tableRef.current?.clearAll();
      tableRef.current?.search();
    } catch (error) {
      setErrorMessage(parseStandardAPIErrorMessage(error));
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        loadingForUpdatedProductIncomeScheme: false,
      }));
    }
  };

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  const refreshFeeTable = () => {
    tableRef?.current?.search();
  };

  const warningPopupMessage = () => {
    return `Changing the Product Income Scheme will</br></br><ul><li>${"Clear entries in Fees Details table"}</li><li>Make changes in Final Loan Details Table</li><li>Clear entries in Income Details table</li><li>Make changes in Monthly Banking Analysis</li><li>Make changes in Credit and Debit Transactions</li></ul><br/>Are you sure you want to continue ?`;
  };

  const getAmountValidations = (configurationType?: TCamConfigurationTypeCode | "") => {
    if (configurationType === CAM_CHARGE_CONFIGURATION_CODE.CONFIGURATION_VARIABLE) {
      return "mandatory|checkNumber|max:100,num|maxDecimal:2";
    }
    return "mandatory";
  };
  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  const enrichForCalculation = (rowData: ICamFeeDetailsRowData) => {
    const enrichObj = {
      loanUid: params?.loanUid,
      stageUid: params?.stageUid,
      feeConfigurationUid: rowData?.feeConfiguration?.uid,
      loanFeeAmount: getNumber(rowData?.amount),
    };
    return enrichObj;
  };

  const enrichForSave = () => {
    console.log("enrich for save");
    let addRows = tableRef.current?.getAddedRecords() ?? [];
    let deletedRows = tableRef.current?.getDeletedRecords() ?? [];
    let updatedRows = tableRef.current?.getUpdatedRecords() ?? [];
    addRows = addRows.map((rowData: any) => updateCamFeeDetailsRowDataObj(rowData));

    deletedRows = deletedRows.map((rowData: any) => updateCamFeeDetailsRowDataObj(rowData));
    updatedRows = updatedRows.map((rowData: any) => updateCamFeeDetailsRowDataObj(rowData));

    const loanFees = [...addRows, ...deletedRows, ...updatedRows];
    return {
      loan: {
        uid: params?.loanUid,
      },
      stage: {
        uid: params?.stageUid,
      },
      productIncomeScheme: {
        uid: pageObj?.productIncomeScheme?.uid,
      },
      loanFees: loanFees,
    };
  };

  const enrichForSaveOnProductIncomeSchemeUpdate = (productIncomeSchemeUid?: string) => {
    return {
      loan: {
        uid: params?.loanUid,
      },
      stage: {
        uid: params?.stageUid,
      },
      productIncomeScheme: {
        uid: productIncomeSchemeUid,
      },
      loanFees: [],
    };
  };

  const debounce = (func: any, delay: any) => {
    // for amount incl gst calc
    clearTimeout(timeOutLocal);
    setTimeOutLocal(setTimeout(func, delay));
  };

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */
  const renderChargeName = (rowData: ICamFeeDetailsRowData) => {
    if (rowData?.feeConfiguration?.isDefault) {
      return (
        <div className="pt-4">
          <SimpleDropDown
            hideLabel
            placeholder="placeholder.cam.chargeName"
            name={`chargeName${rowData?.uid}`}
            value={rowData?.feeConfiguration?.fee?.name}
            variant={INPUT_STYLE_TYPE.PRIMARY}
            options={[`${rowData?.feeConfiguration?.fee?.name}`]}
            disabled
          />
        </div>
      );
    }

    return (
      <div className="pt-4">
        <SimpleDropDown
          hideLabel
          placeholder="placeholder.cam.chargeName"
          name={`chargeName${rowData?.uid}`}
          value={rowData?.feeConfiguration?.uid}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={pageObj?.chargeNameOptions}
          optionLabel="fee.name"
          optionValue="uid"
          onChange={(e) => handleTableChargeNameChange(e, { rowData })}
          onBlur={validate}
          // loader={isLoading}
          validation={simpleValidator?.current?.message(
            `chargeName${rowData?.uid}`,
            rowData.feeConfiguration?.fee?.name,
            "mandatory"
          )}
          disabled={pageObj?.row?.isCalculating && pageObj?.row?.uid !== rowData.uid}
        />
      </div>
    );
  };

  const renderChargeType = (rowData: ICamFeeDetailsRowData) => {
    return (
      <InputBox
        // name={`premiumAmountInclusiveGst${rowData?.uid}`}
        variant={INPUT_STYLE_TYPE.SECONDARY}
        value={
          rowData?.chargeTypeCode !== "" ? CAM_CHARGE_TYPE?.[rowData?.chargeTypeCode]?.label : ""
        }
        disabled
      />
    );
  };

  const renderConfiguration = (rowData: ICamFeeDetailsRowData) => {
    return (
      <InputBox
        // name={`premiumAmountInclusiveGst${rowData?.uid}`}
        variant={INPUT_STYLE_TYPE.SECONDARY}
        value={rowData.feeConfiguration?.configurationType?.name}
        disabled
      />
    );
  };

  const renderWaiver = (rowData: ICamFeeDetailsRowData) => {
    return (
      <InputBox
        // name={`premiumAmountInclusiveGst${rowData?.uid}`}
        variant={INPUT_STYLE_TYPE.SECONDARY}
        value={rowData.feeConfiguration?.isWaiver ? "Yes" : "No"}
        disabled
        // onBlur={validate}
      />
    );
  };

  const renderDefault = (rowData: ICamFeeDetailsRowData) => {
    return (
      <InputBox
        // name={`premiumAmountInclusiveGst${rowData?.uid}`}
        variant={INPUT_STYLE_TYPE.SECONDARY}
        value={rowData.feeConfiguration?.isDefault ? "Yes" : "No"}
        disabled
      />
    );
  };

  const renderAmount = (rowData: ICamFeeDetailsRowData) => {
    return (
      <div className="">
        <InputAddon
          name={`amount${rowData?.uid}`}
          label=""
          // hideLabel
          className="text-right" //need to check
          variant={INPUT_STYLE_TYPE.SECONDARY}
          value={
            rowData?.feeConfiguration?.configurationType?.code ===
            CAM_CHARGE_CONFIGURATION_CODE.CONFIGURATION_FIXED
              ? showCurrency(rowData?.amount)
              : rowData?.amount
          }
          onBlur={validate}
          onChange={(e: any) => handleTableAmountChange(e, { rowData })}
          validation={simpleValidator?.current?.message(
            `amount${rowData?.uid}`,
            rowData?.amount,
            getAmountValidations(rowData?.feeConfiguration?.configurationType?.code)
          )}
          disabled={
            rowData?.chargeTypeCode === CAM_CHARGE_TYPE.CHARGE_TYPE_SYSTEM_DEFINED.code ||
            (pageObj?.row?.isCalculating && pageObj?.row?.uid !== rowData.uid)
          }
          isSuffix
          addOn={
            rowData?.feeConfiguration?.configurationType?.code ===
            CAM_CHARGE_CONFIGURATION_CODE.CONFIGURATION_FIXED
              ? ""
              : rowData?.feeConfiguration?.configurationType?.code ===
                CAM_CHARGE_CONFIGURATION_CODE.CONFIGURATION_VARIABLE
              ? "%"
              : ""
          }
        />
      </div>
    );
  };

  const renderAmountIncludingGst = (rowData: ICamFeeDetailsRowData) => {
    return (
      <InputBox
        variant={INPUT_STYLE_TYPE.SECONDARY}
        className="text-right"
        value={rowData.amountIncludingGst ?? ""}
        disabled
      />
    );
  };

  const renderActions = (rowData: ICamFeeDetailsRowData) => {
    return (
      <div className="">
        <IconButton
          buttonType={BUTTON_TYPES.DELETE}
          onClick={() => onDeleteRow(rowData)}
          disabled={pageMode === PAGE_MODE.VIEW || !rowData?.feeConfiguration?.isWaiver}
          tooltipOptions={{
            position: "left",
            className: "text-xs font-Roboto bg-black text-white !p-1 rounded-md ",
            showDelay: "1000,",
            hideDelay: "300",
          }}
        />
      </div>
    );
  };
  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */
  const { productIncomeScheme } = pageObj;
  return (
    <>
      <SaveComponent
        title="label.cam.schemeDetails"
        pageMode={pageMode}
        disabled={pageObj?.row?.isCalculating}
        onSave={onSave}
        onCancel={onCancel}
        onEdit={onEdit}
      >
        {isLoading || pageObj?.loadingForUpdatedProductIncomeScheme ? (
          <SkeletonCustom height="19.375rem" width="100%" borderRadius="8px" animation="wave" />
        ) : (
          <AclGuard override={{ read: pageMode === PAGE_MODE.VIEW }}>
            <div className=" grid grid-cols-4 gap-3  ">
              <div className="col-span-2">
                <SimpleDropDown
                  label="label.cam.productIncomeScheme*"
                  placeholder="placeholder.cam.productIncomeScheme*"
                  name="productIncomeScheme"
                  value={productIncomeScheme?.uid}
                  onChange={handleProductSchemeChange}
                  validation={simpleValidator?.current?.message(
                    `productIncomeScheme`,
                    productIncomeScheme?.uid,
                    "mandatory"
                  )}
                  onBlur={validate}
                  options={dropDownOptions.productIncomeScheme}
                  optionLabel="name"
                  optionValue="uid"
                />
              </div>
              <div className="col-span-2"></div>
              <div>
                <SimpleDropDown
                  label="label.cam.Product*"
                  placeholder=""
                  name="product"
                  value={productIncomeScheme?.product?.name}
                  options={
                    productIncomeScheme?.product?.name ? [productIncomeScheme.product.name] : []
                  }
                  // optionLabel="n"
                  disabled
                />
              </div>
              <div>
                <SimpleDropDown
                  label="label.cam.incomeScheme*"
                  placeholder=""
                  name="product"
                  value={productIncomeScheme?.incomeSchemeName?.name}
                  options={
                    productIncomeScheme?.incomeSchemeName?.name
                      ? [productIncomeScheme.incomeSchemeName.name]
                      : []
                  }
                  disabled
                />
              </div>
              <div>
                <SimpleDropDown
                  label="label.cam.bankOrFinancierName*"
                  placeholder=""
                  name="bank"
                  value={productIncomeScheme?.bank?.name}
                  onChange={handleChange}
                  options={productIncomeScheme?.bank?.name ? [productIncomeScheme.bank.name] : []}
                  disabled
                />
              </div>
              <div>
                <SimpleDropDown
                  label="label.cam.lendingType*"
                  placeholder=""
                  name="product"
                  value={productIncomeScheme?.lendingType?.name}
                  options={
                    productIncomeScheme?.lendingType?.name
                      ? [productIncomeScheme.lendingType.name]
                      : []
                  }
                  disabled
                />
              </div>
              <div className="col-span-4">
                {typeof pageMode === "string" ? (
                  <>
                    <div className="flex justify-between mb-3">
                      <div>
                        <Text
                          className="text-primary-ptext-900 font-medium text-sm "
                          label={"label.cam.feeDetails"}
                        />
                      </div>
                      <div className="flex ">
                        <div className=" mr-3 self-center">
                          <TextButton
                            label="label.cam.populateDefaultFees"
                            className="text-primary-copyTextColor text-sm cursor-pointer"
                            onClick={onPrePopulateData}
                            disabled={!pageObj?.productIncomeScheme?.uid}
                            //isLoading={tableRef.current?.getLoadingState()}
                          />
                        </div>
                        <div className=" mr-3 self-center">
                          <ButtonBox
                            buttonType={BUTTON_TYPE.ICON_TEXT_ROW}
                            customLabel={"label.cam.addFee"}
                            onClick={onAddDocument}
                          />
                        </div>
                      </div>
                    </div>
                    {pageObj?.tableSearch ? (
                      <EditableTableV2
                        ref={tableRef}
                        uniqueKey="uid"
                        standardDataFormatPromise={CamService.getFeeList}
                        search={pageObj?.tableSearch}
                        mode={"UPDATE"}
                        onUpdateCallback={onTableUpdateCallback}
                        variant="secondary"
                        paginator={false}
                        className="table-auto-height"
                      >
                        <Column
                          header={i18n.t("label.cam.chargeName")}
                          className="text-primary-ptext-1000 text-left text-xs h-12 pl-6 min-w-49"
                          body={renderChargeName}
                        />

                        <Column
                          header={i18n.t("label.cam.chargeType")}
                          className=" text-primary-ptext-1000 text-xs h-12 pl-6 min-w-50"
                          body={renderChargeType}
                        />
                        <Column
                          header={i18n.t("label.cam.configuration")}
                          className=" text-primary-ptext-1000 text-xs h-12 pl-6"
                          body={renderConfiguration}
                        />
                        <Column
                          header={i18n.t("label.cam.waiver")}
                          className=" text-primary-ptext-1000 text-xs h-12 pl-6"
                          body={renderWaiver}
                        />
                        <Column
                          header={i18n.t("label.cam.default")}
                          className=" text-primary-ptext-1000 text-xs h-12 pl-6"
                          body={renderDefault}
                        />
                        <Column
                          header={i18n.t("label.cam.amount/percentage")}
                          className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                          body={renderAmount}
                        />
                        <Column
                          header={i18n.t("label.cam.amountIncludingGst()")}
                          className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                          body={renderAmountIncludingGst}
                        />

                        <Column
                          header={i18n.t("label.investmentInfo.table.colTitle.action")}
                          className="text-primary-ptext-1000 text-right text-xs h-12 px-6"
                          style={{ minWidth: "50px", width: "50px" }}
                          body={renderActions}
                        />
                      </EditableTableV2>
                    ) : null}
                  </>
                ) : null}
              </div>
            </div>
          </AclGuard>
        )}
      </SaveComponent>

      <WarningDialog
        visible={pageObj.isChangeProductIncomeScheme}
        className="  !w-100 !px-14"
        title={"Change Product Income Scheme ?"}
        warningMessage={warningPopupMessage()}
        htmlMessage
        onCancel={onHideWarningPopup}
        onConfirm={onConfirmDeleteTab}
        onHide={() => {
          //
        }}
      />

      <ToastCustom
        position={"bottom-right"}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={TOAST_STYLE_TYPE.PRIMARY}
      />

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </>
  );
});

export default SchemeDetails;
