import AclGuard from "components/AclGuard";
import ButtonBox from "components/ButtonBox/ButtonBox";
import EditableTableV2, {
  Column,
  EditableComponentRef,
} from "components/EditableTableV2/EditableTableV2";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import IconButton, { BUTTON_TYPES } from "components/IconButton/IconButton";
import InputBox from "components/InputBox";
import InputError from "components/InputError/InputError";
import RadioButton from "components/RadioButton/RadioButton";
import SaveComponent from "components/Save/SaveComponent";
import SimpleDropDown from "components/SimpleDropDown/SimpleDropDown";
import Text from "components/Text/Text";
import ToastCustom from "components/Toast";
import i18n from "i18n";
import React, { forwardRef, useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import CamService from "services/transaction/CamService";
import SimpleReactValidator from "simple-react-validator";
import {
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
} from "utils/CommonUtil";
import {
  BUTTON_TYPE,
  CHECKBOX_STYLE_TYPE,
  INPUT_STYLE_TYPE,
  PAGE_MODE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
} from "utils/Constant";
import { updateCamLoanTagsRowDataObj } from "../../Helper";
import { ICamLoanDetailsDropdownList } from "../../PageLoanDetails/PageLoanDetails";

export interface ICamLoanTagsPageObj {
  loanUid: string;
  // properties: ICamSinglePropertyValuationData[];
  // totalValuation: number | "";
}
interface IProps {
  // isLoading: boolean;
  dropDownOptions: ICamLoanDetailsDropdownList;
  // data?: ICamLoanTagsPageObj;
  getDetailsCallback?: () => void;
}

export interface ICamLoanTagsRowData {
  uid?: string;
  smTagName: string;
  isApplicable: boolean | null;
  remarks: string;
  isActive: boolean;
}

export const initialCamLoanTagsData: ICamLoanTagsPageObj = {
  loanUid: "",
  // totalValuation: "",
};

const initialTableRowData: ICamLoanTagsRowData = {
  uid: "",
  smTagName: "",
  remarks: "",
  isApplicable: null,
  isActive: true,
};

const LoanTags: React.FC<IProps> = forwardRef((props, ref) => {
  const { dropDownOptions, getDetailsCallback } = props;

  const params: { loanUid?: string; stageUid?: string } = useParams();

  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
    */

  const [pageMode, setPageMode] = useState(PAGE_MODE.VIEW);
  const [toastMessage, setToastMessage] = useState("");
  const [triggerToast, setTriggerToast] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  // ref
  const toastMessageRef = useRef(null);
  const tableRef = React.useRef<EditableComponentRef>(null);

  // --------------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false);

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  // useEffect(() => {
  //   if (data?.loanUid) {
  //     setPageObj((prevObj) => ({
  //       ...prevObj,
  //       ...data,
  //     }));

  //   }
  // }, [data]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }, [toastMessage, triggerToast]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */

  const handleTableFieldChange = (
    e: any,
    options: {
      rowData: ICamLoanTagsRowData;
      field: keyof ICamLoanTagsRowData;
      isCheckbox?: boolean;
    }
  ) => {
    let rowDataObj: ICamLoanTagsRowData = { ...options.rowData };
    if (options?.isCheckbox) {
      rowDataObj = {
        ...rowDataObj,
        [`${options.field}`]: e.value,
      };
    } else {
      rowDataObj = {
        ...rowDataObj,
        [`${options.field}`]: e.target.value,
      };
    }
    tableRef.current?.reportDataChange(rowDataObj);
  };

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;
    isValid = simpleValidator?.current?.allValid() ?? false;
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  const onSave = async () => {
    if (!validateForm()) {
      return;
    }
    const payload = enrichForSave();
    try {
      await CamService.updateLoanTags(payload);
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
      setTriggerToast(!triggerToast);
      setPageMode(PAGE_MODE.VIEW);
      tableRef.current?.clearAll();
      tableRef.current?.search();
    } catch (error) {
      setErrorMessage(parseStandardAPIErrorMessage(error));
    }
  };

  const onEdit = () => setPageMode(PAGE_MODE.EDIT);

  const onCancel = () => {
    setPageMode(PAGE_MODE.VIEW);
    resetValidator();
    tableRef.current?.clearAll();
    tableRef.current?.search();
  };

  const onAddDocument = () => {
    tableRef.current?.addRow({ ...initialTableRowData });
  };

  const onDeleteRow = (rowData: ICamLoanTagsRowData) => {
    tableRef.current?.deleteRow(rowData);
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
  };

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */
  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  const enrichForSave = () => {
    console.log("enrich for save");
    let addRows = tableRef.current?.getAddedRecords() ?? [];
    let deletedRows = tableRef.current?.getDeletedRecords() ?? [];
    let updatedRows = tableRef.current?.getUpdatedRecords() ?? [];
    addRows = addRows.map((rowData: any) => ({
      ...rowData,
      smTagName: rowData?.smTagName,
    }));

    deletedRows = deletedRows.map((rowData: any) => updateCamLoanTagsRowDataObj(rowData));
    updatedRows = updatedRows.map((rowData: any) => updateCamLoanTagsRowDataObj(rowData));

    const loanTags = [...addRows, ...deletedRows, ...updatedRows];
    return {
      loan: {
        uid: params?.loanUid,
      },
      stage: {
        uid: params?.stageUid,
      },
      loanTags: loanTags,
    };
  };

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */
  const renderTags = (rowData: ICamLoanTagsRowData) => {
    return (
      <div className="pt-4">
        <SimpleDropDown
          hideLabel
          placeholder="placeholder.cam.Tags"
          name={`tags${rowData?.uid}`}
          value={rowData?.smTagName}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={dropDownOptions.tags}
          optionLabel="name"
          optionValue="code"
          onChange={(e) => handleTableFieldChange(e, { rowData, field: "smTagName" })}
          onBlur={validate}
          validation={simpleValidator?.current?.message(
            `tags${rowData?.uid}`,
            rowData.smTagName,
            "mandatory"
          )}
        />
      </div>
    );
  };

  const renderYesOrNo = (rowData: ICamLoanTagsRowData) => {
    return (
      <div className="mt-5 ">
        <div className="flex flex-shrink-0 justify-center ">
          <div className="mr-5 flex">
            <RadioButton
              hideLabel
              hideErrorRow
              variant={CHECKBOX_STYLE_TYPE.PRIMARY}
              value={true}
              checked={rowData?.isApplicable === true}
              onChange={(e) =>
                handleTableFieldChange(e, { rowData, field: "isApplicable", isCheckbox: true })
              }
              disabled={pageMode === PAGE_MODE.VIEW}
            />
            <Text className="ml-1.5 text-xs " label="label.dde.yes" />
          </div>
          <div className="flex">
            <RadioButton
              hideLabel
              hideErrorRow
              variant={CHECKBOX_STYLE_TYPE.PRIMARY}
              value={false}
              checked={rowData?.isApplicable === false}
              onChange={(e) =>
                handleTableFieldChange(e, { rowData, field: "isApplicable", isCheckbox: true })
              }
              disabled={pageMode === PAGE_MODE.VIEW}
            />
            <Text className="ml-1.5 text-xs " label="label.dde.no" />
          </div>
        </div>
        <div className="h-4 flex ml-6 justify-center mb-1.5 ">
          <InputError
            validation={simpleValidator?.current?.message(
              `value${rowData?.uid}`,
              rowData.isApplicable,
              "mandatory"
            )}
          />
        </div>
      </div>
    );
  };

  const renderComment = (rowData: ICamLoanTagsRowData) => {
    return (
      <div className="flex items-start mt-1">
        <InputBox
          name={`comment${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          onChange={(e) => handleTableFieldChange(e, { rowData, field: "remarks" })}
          onBlur={validate}
          value={rowData?.remarks}
          validation={simpleValidator?.current?.message(
            `comment${rowData?.uid}`,
            rowData?.remarks,
            "mandatory"
          )}
          // hideLabel
          // hideErrorRow
        />
      </div>
    );
  };

  const renderActions = (rowData: any) => {
    return (
      <div className="">
        <IconButton
          buttonType={BUTTON_TYPES.DELETE}
          onClick={() => onDeleteRow(rowData)}
          disabled={pageMode === PAGE_MODE.VIEW}
          tooltipOptions={{
            position: "left",
            className: "text-xs font-Roboto bg-black text-white !p-1 rounded-md ",
            showDelay: "1000,",
            hideDelay: "300",
          }}
        />
      </div>
    );
  };
  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */
  return (
    <>
      <SaveComponent
        title="label.cam.loanTags"
        pageMode={pageMode}
        // disabled={isLoading || isLegalSectionAccessDisabled(legalStatus)}
        onSave={onSave}
        onCancel={onCancel}
        onEdit={onEdit}
      >
        <AclGuard override={{ read: pageMode === PAGE_MODE.VIEW }}>
          <div className=" grid grid-cols-4 gap-3  ">
            <div className="col-span-4">
              {typeof pageMode === "string" ? (
                <>
                  <div className="flex mb-3 justify-end">
                    <div className=" mr-3 self-center">
                      <ButtonBox
                        buttonType={BUTTON_TYPE.ICON_TEXT_ROW}
                        customLabel={"label.cam.addTags"}
                        onClick={onAddDocument}
                      />
                    </div>
                  </div>
                  <EditableTableV2
                    ref={tableRef}
                    uniqueKey="uid"
                    standardDataFormatPromise={CamService.getLoanTags}
                    search={`?stage.uid=${params?.stageUid}&loan.uid=${
                      params?.loanUid
                    }&isActive=${true}`}
                    mode={"UPDATE"}
                    className="table-auto-height"
                  >
                    <Column
                      header={i18n.t("label.cam.tags")}
                      className="text-primary-ptext-1000 text-left text-xs h-12 pl-6"
                      body={renderTags}
                    />

                    <Column
                      header={i18n.t("label.cam.yes/no")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderYesOrNo}
                    />
                    <Column
                      header={i18n.t("label.cam.comment")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderComment}
                    />

                    <Column
                      header={i18n.t("label.investmentInfo.table.colTitle.action")}
                      className="text-primary-ptext-1000 text-right text-xs h-12 px-6"
                      style={{ minWidth: "50px", width: "50px" }}
                      body={renderActions}
                    />
                  </EditableTableV2>
                </>
              ) : null}
            </div>
          </div>
        </AclGuard>
      </SaveComponent>

      <ToastCustom
        position={"bottom-right"}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={TOAST_STYLE_TYPE.PRIMARY}
      />

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </>
  );
});

export default LoanTags;
