import AclGuard from "components/AclGuard";
import { Column, EditableComponentRef } from "components/EditableTableV2/EditableTableV2";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import ReadOnlyTable from "components/ReadOnlyTable/ReadOnlyTable";
import SaveComponent from "components/Save/SaveComponent";
import Section from "components/Section/Section";
import SimpleDropDown from "components/SimpleDropDown/SimpleDropDown";
import Text from "components/Text/Text";
import ToastCustom from "components/Toast";
import i18n from "i18n";
import React, { forwardRef, useEffect, useImperativeHandle, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import CamService from "services/transaction/CamService";
import SimpleReactValidator from "simple-react-validator";
import {
  formatDate,
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
} from "utils/CommonUtil";
import {
  INPUT_STYLE_TYPE,
  PAGE_MODE,
  PROPERTY_ANNOTATION,
  PROPERTY_VALUATION_OPERATION_OPTIONS,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TCamPropertyValuationOperation,
  TOAST_STYLE_TYPE,
} from "utils/Constant";
import { getNumber, showCurrency } from "../../Helper";
import SkeletonCustom from "components/Skeleton/Skeleton";

export interface ICamCollateralPropertyData {
  uid: string; // propertyAddress Uid
  address: string;
  pincode: {
    pinCode: string;
  };
  city: {
    name: string;
    state: {
      name: string;
    };
  };
  valuationTable: ICamValuationRowData[];
  pageMode: string;
  propertyValuation: {
    // field will not come for the first time
    smValuationOperationType?: TCamPropertyValuationOperation;
    finalValuation?: string | number;
  };
  isPropertyValuatedByMethodOperation: boolean; // it will be false  for First time
  // valuationMethod: string;
  // calculatedValuation: number | "";
}
export interface ICamPropertyValuationData {
  collateralProperties: ICamCollateralPropertyData[];
  totalPropertyValuation: string | number; // sum of all the cards
  property?: {
    uid: string;
    isCalculating: boolean;
  };

  // totalValuation: number | "";
}
interface IProps {
  isLoading: boolean;
  // dropDownOptions: ICamLoanDetailsDropdownList;
  data?: ICamPropertyValuationData;
  getDetailsCallback?: (onlyFinalTotal?: boolean) => void;
}

export interface ICamValuationRowData {
  uid: string; //stage uid
  partner: {
    name: string;
  };
  stageCompletionDate: string;
  technical: {
    govtTotalValuationOfProperty: number | string;
  };
}
export const initialCamPropertyValuationData: ICamPropertyValuationData = {
  collateralProperties: [],
  totalPropertyValuation: "",
  property: {
    uid: "",
    isCalculating: false,
  },
  // totalValuation: "",
};

const PropertyValuation: React.FC<IProps> = forwardRef((props, ref) => {
  const { data, getDetailsCallback, isLoading } = props;
  const params: { loanUid?: string; stageUid?: string } = useParams();
  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
    */
  const [pageObj, setPageObj] = useState<ICamPropertyValuationData>({
    ...initialCamPropertyValuationData,
  });
  // const [pageMode, setPageMode] = useState(PAGE_MODE.VIEW);
  const [toastMessage, setToastMessage] = useState("");
  const [triggerToast, setTriggerToast] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  // ref
  const toastMessageRef = useRef(null);
  const tableRef = React.useRef<EditableComponentRef>(null);

  // --------------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false);

  const simpleValidator = useRef<any[]>([]);
  const actionMenuRef = useRef<any>(null);
  const premiumAmountHeaderRef = useRef<any>(null);

  useImperativeHandle(ref, () => ({
    refresh: onSearch,
  }));

  // --------------------------- global variables ---------------------- //
  const actionMenuModel: any[] = [
    {
      items: [],
    },
  ];

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  useEffect(() => {
    setPageObj((prevObj) => ({
      ...prevObj,
      collateralProperties: data?.collateralProperties ?? [],
      totalPropertyValuation: data?.totalPropertyValuation ?? "",
    }));
    // when add the properties  add the  validations for the properties
    resetAllSectionValidations(data?.collateralProperties?.length ?? 0);
  }, [data]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }, [toastMessage, triggerToast]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  const getPropertyValuationByOperation = async (property: ICamCollateralPropertyData) => {
    setPageObj((prevObj) => ({
      ...prevObj,
      property: {
        ...prevObj.property,
        isCalculating: true,
        uid: property?.uid,
      },
    }));
    try {
      const payload = enrichForCalculation(property);
      const res = await CamService.engine(payload);
      // data
      
      setPageObj((prevObj) => {
        const updatedObj ={...prevObj, 
          property: {
            ...prevObj.property,
            isCalculating: false,
            uid: "",
          },

        };
        const propertyIndex =prevObj.collateralProperties?.findIndex(record=>record.uid===property?.uid);
        // ...prevObj,
        updatedObj.collateralProperties[propertyIndex].propertyValuation.finalValuation= showCurrency(res?.data?.propertyFinalValuation);
        return updatedObj;
      });
      // tableRef.current?.reportDataChange(updatedRowDataObj);
    } catch (err) {
      console.log(err);
      setPageObj((prevObj) => ({
        ...prevObj,
        property: {
          ...prevObj.property,
          isCalculating: false,
          uid:"",
        },
      }));
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */
  const handleValuationMethodChange = (
    e: any,
    options: { index: number; property: ICamCollateralPropertyData }
  ) => {
    setPageObj((prevObj) => {
      const updatedPageObj = { ...prevObj };
      updatedPageObj.collateralProperties[
        options.index
      ].propertyValuation.smValuationOperationType = e.target.value;
      getPropertyValuationByOperation(options.property);
      return updatedPageObj;
    });
  };

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  const validate = (e: any, index: number) => {
    simpleValidator?.current?.[index]?.showMessageFor(e?.target?.name);
  };

  const validateForm = (index: number) => {
    let isValid = false;
    isValid = simpleValidator?.current?.[index]?.allValid() ?? false;
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.[index]?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  const onSave = async (index: number, options: { data: ICamCollateralPropertyData }) => {
    console.log("save");

    if (!validateForm(index)) {
      return;
    }
    const payload = enrichForSave(options?.data);
    try {
      await CamService.updatePropertyValuation(payload);
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
      setTriggerToast(!triggerToast);
      getDetailsCallback?.(true);
      setPageObj((prevPageObj) => {
        const updatedPageObj = { ...prevPageObj };
        updatedPageObj.collateralProperties[index].pageMode = PAGE_MODE.VIEW;
        return updatedPageObj;
      });
    } catch (error) {
      setErrorMessage(parseStandardAPIErrorMessage(error));
    }
  };

  const onEdit = (index: number) => {
    // setPageMode(PAGE_MODE.EDIT);
    setPageObj((prevPageObj) => {
      const updatedPageObj = { ...prevPageObj };
      updatedPageObj.collateralProperties[index].pageMode = PAGE_MODE.EDIT;
      return updatedPageObj;
    });
  };

  const onCancel = (index: number) => {
    setPageObj((prevPageObj) => {
      const updatedPageObj = { ...prevPageObj };
      updatedPageObj.collateralProperties[index].pageMode = PAGE_MODE.VIEW;
      return updatedPageObj;
    });
    getDetailsCallback?.();
    resetSectionValidations(index);
  };

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  const enrichForCalculation = (data: ICamCollateralPropertyData) => {
    const enrichObj = {
      loanUid: params?.loanUid,
      stageUid: params?.stageUid,
      smPropertyValuationOperationType: data?.propertyValuation?.smValuationOperationType,
      propertyAddressUid: data?.uid,
      // recommendedLoanAmount: getNumber(data?.recommendedLoanAmount),
    };
    return enrichObj;
  };

  const enrichForSave = (data: ICamCollateralPropertyData) => {
    const enrichObj = {
      loan: {
        uid: params?.loanUid,
      },
      stage: {
        uid: params?.stageUid,
      },
      address: {
        uid: data?.uid,
      },
      smValuationOperationType: data?.propertyValuation?.smValuationOperationType,
      finalValuation: getNumber(data?.propertyValuation?.finalValuation),
    };
    return enrichObj;
  };

  const resetAllSectionValidations = (totalSections: number) => {
    for (let i = 0; i < totalSections; i++) {
      simpleValidator.current[i] = new SimpleReactValidator({
        validators: { ...getStandardValidator() },
        ...messageReplace(),
        autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
      });
      simpleValidator?.current?.[i]?.hideMessages();
    }
  };

  const resetSectionValidations = (index: number) => {
    simpleValidator.current[index] = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.[index]?.hideMessages();
  };

  const onSearch = () => {
    tableRef.current?.search();
  };

  const getAllPropertyAnnotations = () => {
    let annotations = "";
    for (let i = 0; i < pageObj.collateralProperties.length; i++) {
      if (i === pageObj.collateralProperties.length - 1) {
        annotations += ` ${PROPERTY_ANNOTATION[i]}`;
      } else {
        annotations += `${PROPERTY_ANNOTATION[i]} + `;
      }
    }

    if (pageObj.collateralProperties.length > 0) {
      return `( ${annotations} )`;
    }
    return "";
  };

  // const getAllPropertiesTotalValuation = () => {
  //   let totalValuation = 0;
  //   pageObj.properties.forEach((property, index) => {
  //     if (property.calculatedValuation === "") {
  //       totalValuation += 0;
  //       return;
  //     }
  //     totalValuation += property.calculatedValuation;
  //   });
  //   return ` ${totalValuation ? " " + numberFormatter(totalValuation, "en-IN", 0) : ""}`;
  // };

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */

  const renderValuationMethod = (property: ICamCollateralPropertyData, index: number) => {
    return (
      <div className="flex ">
        <div className="w-30 mr-3">
          <SimpleDropDown
            hideLabel
            // placeholder="placeholder.cam.typeOfApplicant"
            name={`smValuationOperationType${index}`}
            value={property?.propertyValuation?.smValuationOperationType ?? ""}
            variant={INPUT_STYLE_TYPE.PRIMARY}
            options={PROPERTY_VALUATION_OPERATION_OPTIONS}
            optionLabel="name"
            optionValue="code"
            onChange={(e) => handleValuationMethodChange(e, { index, property })}
            onBlur={(e) => validate(e, index)}
            // loader={isLoading}
            validation={simpleValidator?.current?.[index].message(
              `smValuationOperationType${index}`,
              property.propertyValuation?.smValuationOperationType,
              "mandatory"
            )}
            disabled={pageObj?.property?.isCalculating && pageObj?.property?.uid !== property?.uid}
          />
        </div>
        <Text className=" text-xs font-medium " label={`(${PROPERTY_ANNOTATION[index]})`} />
      </div>
    );
  };

  const renderCalculatedValuation = (property: ICamCollateralPropertyData) => {
    return (
      <Text
        className="text-right"
        label={`${
          property.propertyValuation?.finalValuation
            ? " " + showCurrency(property.propertyValuation?.finalValuation)
            : ""
        }`}
      />
    );
  };

  const renderVenderName = (rowData: ICamValuationRowData) => {
    return (
      <Text className="  text-primary-ptext-1000 " label={`${rowData?.partner?.name ?? ""}`} />
    );
  };

  const renderDateOfValuation = (rowData: ICamValuationRowData) => {
    return (
      <Text
        className="  text-primary-ptext-1000 "
        label={`${formatDate(rowData?.stageCompletionDate)}`}
      />
    );
  };

  const renderTotalValuation = (rowData: ICamValuationRowData) => {
    return (
      <Text
        className="  text-primary-ptext-1000 text-right"
        label={`${showCurrency(rowData?.technical?.govtTotalValuationOfProperty)}`}
      />
    );
  };

  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */

  return (
    <Section title="label.technical.propertyValuation">
      <>
        {isLoading ? (
          <SkeletonCustom height="19.375rem" width="100%" borderRadius="8px" animation="wave" />
        ) : (
          <>
            {pageObj.collateralProperties.map(
              (property: ICamCollateralPropertyData, index: number) => (
                <div key={index}>
                  <SaveComponent
                    title=""
                    // renderHeaderContent={renderSaveComponentHeader}
                    // disabled={isLegalSectionAccessDisabled(legalStatus)}
                    className=""
                    pageMode={property.pageMode}
                    disabled={pageObj?.property?.isCalculating}
                    onSave={() => onSave(index, { data: property })}
                    onCancel={() => onCancel(index)}
                    onEdit={() => onEdit(index)}
                  >
                    <AclGuard override={{ read: property.pageMode === PAGE_MODE.VIEW }}>
                      <div className="grid grid-cols-4 gap-5">
                        <div className="flex flex-col">
                          <Text
                            className="text-primary-gray-600 text-xs"
                            label={"label.dde.propertyAddress"}
                          />
                          <Text
                            className="text-primary-ptext-1000 text-xs "
                            label={property?.address ?? ""}
                          />
                        </div>
                        <div className="flex flex-col">
                          <Text
                            className="text-primary-gray-600 text-xs"
                            label={"heading.pincode"}
                          />
                          <Text
                            className="text-primary-ptext-1000 text-xs"
                            label={property?.pincode?.pinCode ?? ""}
                          />
                        </div>
                        <div className="flex flex-col">
                          <Text
                            className="text-primary-gray-600 text-xs"
                            label={"input.pincode.city"}
                          />
                          <Text
                            className="text-primary-ptext-1000 text-xs"
                            label={property?.city?.name ?? ""}
                          />
                        </div>
                        <div className="flex flex-col">
                          <Text
                            className="text-primary-gray-600 text-xs"
                            label={"input.pincode.state"}
                          />
                          <Text
                            className="text-primary-ptext-1000 text-xs"
                            label={property?.city?.state?.name ?? ""}
                          />
                        </div>
                        <div className="col-span-4">
                          <div className="mb-3">
                            <Text
                              className="text-primary-ptext-1000  text-xs "
                              label={"label.cam.valuation"}
                            />
                          </div>
                          <ReadOnlyTable
                            value={property?.valuationTable}
                            className={`transaction ${
                              property?.valuationTable?.length
                                ? "  "
                                : " rcu-stage-document-verification"
                            }`}
                            ref={tableRef}
                            dataKey="uid"
                          >
                            <Column
                              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                              header={i18n.t("label.cam.vendorName")}
                              body={renderVenderName}
                              footer={() => renderValuationMethod(property, index)}
                            />
                            <Column
                              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                              header={i18n.t("label.cam.dateOfValuation")}
                              body={renderDateOfValuation}
                            />
                            <Column
                              className="text-primary-ptext-1000 text-right text-xs  p-1 h-12 pl-6 pr-5"
                              header={i18n.t("label.cam.totalValuation()")}
                              body={renderTotalValuation}
                              style={{ maxWidth: "168px" }}
                              footer={() => renderCalculatedValuation(property)}
                            />
                          </ReadOnlyTable>
                        </div>
                      </div>
                    </AclGuard>
                  </SaveComponent>
                </div>
              )
            )}
            {pageObj?.collateralProperties.length > 0 ? (
              <div className="bg-[#fbfbff] flex justify-between pl-7 pr-11 py-6">
                <div>
                  <Text
                    className=" font-medium text-xs"
                    label={"label.cam.totalValuationBeingConsidered"}
                    args={{ properties: getAllPropertyAnnotations() }}
                  />
                </div>
                <div>
                  <Text
                    className=" font-medium text-xs"
                    label={
                      pageObj?.totalPropertyValuation
                        ? " " + showCurrency(pageObj?.totalPropertyValuation)
                        : ""
                    }
                  />
                </div>
              </div>
            ) : null}
          </>
        )}
        <div></div>

        <ToastCustom
          position={"bottom-right"}
          ref={toastMessageRef}
          toastText={toastMessage}
          variant={TOAST_STYLE_TYPE.PRIMARY}
        />

        <ErrorDialog
          onHide={() => {
            setShowDialogAPIError(false);
            setErrorMessage("");
          }}
          visible={showDialogAPIError}
          errorMessage={errorMessage}
        />
      </>
    </Section>
  );
});

export default PropertyValuation;
